import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IconModule } from '@backbase/ui-ang/icon';
import {
  ProductSummaryAccountLogoComponent,
  ProductSummaryAccountLogoCustomizableDirective,
} from './bb-product-summary-account-logo.component';

const uiModules = [IconModule];
const components = [ProductSummaryAccountLogoComponent];
const directives = [ProductSummaryAccountLogoCustomizableDirective];

/**
 * Exports a UI components for displaying account logo
 *
 * The following components are included in this module:
 *  - `ProductSummaryAccountLogoComponent`
 *
 * @see ProductSummaryAccountLogoComponent
 *
 * @usageNotes
 *
 * ### Include the exported widgets in your `AppModule`:
 *
 * ```typescript
 * @NgModule({
 *   imports: [
 *     ProductSummaryAccountLogoModule,
 *   ],
 *   ...
 * })
 * export class AppModule {}
 * ```
 */
@NgModule({
  imports: [CommonModule, ...uiModules],
  declarations: [...components, ...directives],
  exports: [...components, ...directives, ...uiModules],
})
export class ProductSummaryAccountLogoModule {}
