import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';
import {
  toKebabCase,
  getProductKindIconName,
  DomAttributesService,
  AccountNavigationType,
  DefaultAllowedNavigationTypes,
} from '@backbase/product-summary-common-ang';

@Component({
  selector: 'bb-product-item-card',
  templateUrl: 'product-item-card-ui.component.html',
})
export class ProductItemCardComponent {
  readonly hostRef: ProductItemCardComponent = this;
  /**
   * A reference to AccountNavigationType enum
   */
  readonly accountNavigationType = AccountNavigationType;
  /**
   * Unique id for account-name DOM element
   */
  readonly accountNameId = this.domAttributesService.generateId();
  /**
   * Allowed navigation types, change it to hide some of the navigation links
   *
   * @default DefaultAllowedNavigationTypes
   */
  @Input() allowedNavigations: AccountNavigationType[] = DefaultAllowedNavigationTypes;
  /**
   * Flag that indicates if changing favorite status is allowed
   *
   * @default true
   */
  @Input() canToggleFavorite = true;
  /**
   * Is item set as favorite
   */
  @Input() isFavorite = false;
  /**
   * Product summary Item array - list of accounts data that this component displays.
   */
  @Input() item: ProductSummaryItem | undefined;
  /**
   * itemSelect emits selected account when card is selected
   */
  @Output() itemSelect: EventEmitter<ProductSummaryItem | undefined> = new EventEmitter();
  /**
   * favorite emits selected account as ProductsummaryItem object
   */
  @Output() favorite: EventEmitter<ProductSummaryItem> = new EventEmitter<ProductSummaryItem>();
  /**
   * viewTransactions emits id of clicked account as string - used to navigate to transactions
   */
  @Output() viewTransactions: EventEmitter<any> = new EventEmitter<any>();
  /**
   * viewStatements emits selected account id as string - used to navigate to statements
   */
  @Output() viewStatements: EventEmitter<any> = new EventEmitter<any>();
  /**
   * newPayment emits id of clicked account as string - used to navigate to payments
   */
  @Output() newPayment: EventEmitter<any> = new EventEmitter<any>();
  /**
   * viewBalanceHistory emits selected account id as string - used to navigate to balance history
   */
  @Output() viewBalanceHistory: EventEmitter<any> = new EventEmitter<any>();
  /**
   * @internal
   * @param domAttributesService
   */
  constructor(private readonly domAttributesService: DomAttributesService) {}

  get favoriteIcon(): string {
    return this.isFavorite ? 'star' : 'star-border';
  }

  getIconName(productKindName = ''): string {
    return getProductKindIconName(productKindName);
  }

  getIconColor(productKindName = ''): string {
    return toKebabCase(productKindName);
  }
  /**
   * emits "itemSelected" event
   */
  onItemSelected() {
    this.itemSelect.emit(this.item);
  }

  onFavorite(account: ProductSummaryItem | undefined) {
    this.favorite.emit(account);
  }

  onViewTransactions(id: string) {
    this.viewTransactions.emit(id);
  }

  onNewPayment(id: string) {
    this.newPayment.emit(id);
  }

  onViewStatements(id: string) {
    this.viewStatements.emit(id);
  }

  onViewBalanceHistory(id: string) {
    this.viewBalanceHistory.emit(id);
  }
}
