import { Component, ElementRef, EventEmitter, Input, Output, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { AccountArrangementItem as ProductType } from '@backbase/data-ang/arrangements';
import { PaymentCard } from '@backbase/ui-ang/payment-card';
import { AccountAliasDisplayingLevel, ResponsiveContainerComponent } from '@backbase/product-summary-common-ang';
import { CreditLimitViewMode } from '../bb-credit-limit-ui/types';
import { CreditCardBackgroundType } from './types';

/**
 * Displays account graphical header
 *
 * @usageNotes
 *
 * ### Usage example
 * ```
 * <bb-account-graphical-header
 *   [product]="product"
 *   [canEditAlias]="true"
 *   [useShortCurrency]="false"
 *   [creditLimitViewMode]="emptied"
 *   [canRepay]="true"
 *   [canCashInAdvance]="true"
 *   [creditCardBackgroundType]="black-card"
 *   (aliasUpdated)="onAliasUpdated($event)"
 *   (repayInitiated)="onRepayInitiated()"
 *   (cashAdvanceInitiated)="onCashAdvanceInitiated()"
 * ></bb-account-graphical-header>
 * ```
 *
 * @NgModule AccountGraphicalHeaderModule
 */
@Component({
  selector: 'bb-account-graphical-header',
  templateUrl: './account-graphical-header.component.html',
})
export class AccountGraphicalHeaderComponent extends ResponsiveContainerComponent implements AfterViewInit {
  /**
   * AccountArrangementItem to display credit limit
   *
   * @default undefined
   */
  @Input() product: ProductType | undefined;
  /**
   * Defines visibility level of the account alis.
   *
   * @default 'USER'
   */
  @Input() aliasLevel = AccountAliasDisplayingLevel.USER;
  /**
   * Defines if alias can be edited
   *
   * @default false
   */
  @Input() canEditAlias = false;
  /**
   * Whether currency should be displayed as a sign.
   *
   * @default true
   */
  @Input() useShortCurrency = true;
  /**
   * Credit Limit view mode. Can be "filled" or "emptied"
   *
   * @default "emptied"
   */
  @Input() creditLimitViewMode: CreditLimitViewMode = 'emptied';
  /**
   * Defines if quick repay is allowed
   *
   * @default false
   */
  @Input() canRepay = false;
  /**
   * Defines if cash in advance is allowed
   *
   * @default false
   */
  @Input() canCashInAdvance = false;
  /**
   * Credit card background type
   *
   * @default "black-card"
   */
  @Input() creditCardBackgroundType: CreditCardBackgroundType = 'black-card';
  /**
   * Defines visibility of the logo
   *
   * @default false
   */
  @Input() isLogoVisible = false;
  /**
   * Event (Output) that emits when item alias is changed
   */
  @Output() aliasUpdated = new EventEmitter<string>();
  /**
   * Event (Output) that emits when `repay` action initiated
   */
  @Output() repayInitiated = new EventEmitter<string>();
  /**
   * Event (Output) that emits when `cash advance` action initiated
   */
  @Output() cashAdvanceInitiated = new EventEmitter<string>();

  /**
   * @internal
   */
  constructor(private readonly el: ElementRef<HTMLElement>, private readonly changeDetector: ChangeDetectorRef) {
    super(el, changeDetector);
  }

  /**
   * A configuration for graphical presentation of payment card
   */
  get graphicalCreditCardConfiguration(): PaymentCard {
    return {
      number: this.product?.number ?? '',
      vendor: this.product?.cardDetails?.cardProvider ?? '',
      name: this.product?.accountHolderNames ?? '',
      expirationDate: this.product?.validThru ? new Date(this.product?.validThru) : ((undefined as any) as Date),
      cardName: this.creditCardBackgroundType,
    };
  }

  /**
   * Event handler for the `click` event of `repay-button`;
   * Emits `arrangementId` to `repayInitiated`
   */
  repayButtonClicked() {
    this.repayInitiated.emit(this.getArrangementId());
  }

  /**
   * Event handler for the `click` event of `cash-advance-button`;
   * Emits `arrangementId` to `cashAdvanceInitiated`
   */
  cashAdvanceButtonClicked() {
    this.cashAdvanceInitiated.emit(this.getArrangementId());
  }
  /**
   * Returns arrangement id of the product
   *
   * @private
   */
  private getArrangementId(): string | undefined {
    return this.product?.id;
  }
}
