import { Component, Directive, Input, OnChanges } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { AccountState } from '@backbase/product-summary-common-ang';

@Component({
  selector: 'bb-account-state-ui',
  templateUrl: 'account-state-ui.component.html',
})
export class AccountStateComponent implements OnChanges {
  readonly hostRef = this;

  /**
   * Product summary account state indicator display flag.
   */
  @Input() isDisplayingAlert = true;
  /**
   * Product summary Account state data that this component displays.
   */
  @Input() item: AccountState | undefined;

  isAlertCLosed = false;

  /**
   * @internal
   */
  ngOnChanges(): void {
    this.isAlertCLosed = false;
  }

  closeAlert() {
    this.isAlertCLosed = true;
  }
}

@Directive({
  selector: '[bbAccountStateCustomizable]',
})
export class AccountStateCustomizableDirective extends BbTemplate<AccountStateComponent, undefined> {}
