import {
  Component,
  Input,
  Directive,
  Output,
  EventEmitter,
  OnInit,
  OnDestroy,
  ElementRef,
  ChangeDetectorRef,
} from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  Arrangement,
  getAccountAlias,
  hasExternalInfo,
  ResponsiveContainerComponent,
} from '@backbase/product-summary-common-ang';
import { distinctUntilChanged } from 'rxjs/operators';
import { Subject, Subscription } from 'rxjs';

/**
 * Displays product kind item
 *
 * @usageNotes
 *
 * ```html
 * <bb-product-kind
 *   [item]="item"
 * ></bb-product-kind>
 * ```
 *
 * @ngModule ProductKindModule
 */
@Component({
  selector: 'bb-product-kind',
  templateUrl: 'product-kind-ui.component.html',
})
export class ProductKindComponent extends ResponsiveContainerComponent implements OnInit, OnDestroy {
  readonly hostRef = this;
  /**
   * A method to return the alias of the given account
   */
  readonly getAlias: (item: any) => string | undefined = getAccountAlias;
  /**
   * Returns true if product has external info
   */
  readonly hasExternalInfo: (product: Arrangement) => boolean = hasExternalInfo;

  /**
   * Product summary Item  data that this component displays.
   */
  @Input() item: any;

  /**
   * Emits ProductKind when selected
   */
  @Output() selectProductKind: EventEmitter<any> = new EventEmitter();

  /**
   * A flag specifies whether to display logo
   */
  @Input() showLogo: boolean | undefined;

  private readonly uniqueSelectProductKindEvent: Subject<Event> = new Subject<Event>();
  private readonly subscriptions: Array<Subscription> = [];

  /**
   * @internal
   */
  constructor(element: ElementRef<HTMLElement>, changeDetector: ChangeDetectorRef) {
    super(element, changeDetector);
  }

  /**
   * @internal
   */
  ngOnInit() {
    const uniqueSelectProductKindSubscription = this.uniqueSelectProductKindEvent
      .pipe(distinctUntilChanged())
      .subscribe(() => this.selectProductKind.emit(this.item));
    this.subscriptions.push(uniqueSelectProductKindSubscription);
  }
  /**
   * @internal
   */
  ngOnDestroy() {
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
    this.uniqueSelectProductKindEvent.complete();
  }
  /**
   * Emits `selectProductKind` event
   */
  onSelectProductKind(event: Event = new Event('default')) {
    this.uniqueSelectProductKindEvent.next(event);
  }
}

@Directive({
  selector: '[bbProductKindCustomizable]',
})
export class ProductKindCustomizableDirective extends BbTemplate<ProductKindComponent, undefined> {}
