import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-chart-legend-ui',
  templateUrl: 'chart-legend.component.html',
})
export class ChartLegendComponent {
  readonly hostRef = this;
  /**
   * Currency data
   */
  @Input() currency = '';
  /**
   * Maximum value
   */
  @Input() maxValue = '';
  /**
   * Minimum value
   */
  @Input() minValue = '';
  /**
   * Color for positive area
   */
  @Input() positiveColor = '';
  /**
   * Color for negative area
   */
  @Input() negativeColor = '';
}

@Directive({
  selector: '[bbChartLegendCustomizable]',
})
export class ChartLegendCustomizableDirective extends BbTemplate<ChartLegendComponent, undefined> {}
