//TO DO add this component to ui-ang as `bb-account-selector-ui`
import {
  Component,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  TemplateRef,
  Directive,
  ContentChild,
  ViewEncapsulation,
} from '@angular/core';

@Directive({ selector: 'ng-template[bbCustomSingleSelectedItemLabel]' })
export class CustomSingleSelectedItemLabelDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomMultiSelectedItemsLabel]' })
export class CustomMultiSelectedItemsLabelDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomOptionsHeader]' })
export class CustomOptionsHeaderDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomOptionItem]' })
export class CustomOptionItemDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomGroupItemsHeader]' })
export class CustomGroupItemsHeaderDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomLoadingTemplate]' })
export class CustomLoadingTemplateDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Component({
  selector: 'bb-account-picker',
  templateUrl: './account-picker-ui.component.html',
  styleUrls: ['./account-picker-ui.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class AccountPickerComponent {
  readonly hostRef: AccountPickerComponent = this;

  @ContentChild(CustomSingleSelectedItemLabelDirective)
  customSingleSelectedItemLabel: CustomSingleSelectedItemLabelDirective | undefined;
  @ContentChild(CustomMultiSelectedItemsLabelDirective)
  customMultiSelectedItemsLabel: CustomMultiSelectedItemsLabelDirective | undefined;
  @ContentChild(CustomOptionsHeaderDirective)
  customOptionsHeader: CustomOptionsHeaderDirective | undefined;
  @ContentChild(CustomOptionItemDirective)
  customOptionItem: CustomOptionItemDirective | undefined;
  @ContentChild(CustomGroupItemsHeaderDirective)
  customGroupItemsHeader: CustomGroupItemsHeaderDirective | undefined;
  @ContentChild(CustomLoadingTemplateDirective)
  customLoadingTemplate: CustomLoadingTemplateDirective | undefined;

  /**
   *  Event (Output) that emits when search is performed. Outputs search term
   */
  @Output() search: EventEmitter<string> = new EventEmitter<string>();
  /**
   *  Event (Output) that emits when clear search
   */
  @Output() clearSearch: EventEmitter<void> = new EventEmitter<void>();
  /**
   *  Event (Output) that emits when scrollToEnd is triggered
   */
  @Output() scrollToEnd: EventEmitter<void> = new EventEmitter<void>();
  /**
   *  Event (Output) that emits when item is removed
   */
  @Output() remove: EventEmitter<any> = new EventEmitter<any>();
  /**
   *  Event (Output) that emits when item is added to selected items or removed from selected items.
   * When Multiple is true: Output is Items[]
   * When Multuple is false: Output is Item
   * TODO: rename 'change' output to avoid collisions with a native 'change' event
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() change: EventEmitter<any> = new EventEmitter<any>();
  /**
   * Items array that this component displays
   */
  @Input() items: any | any[] = [];
  /**
   * Selected items
   */
  @Input() selectedItems: any | any[];
  /**
   * Flag that indicates if select of multiple items is allowed
   */
  @Input() multiple = false;
  /**
   * Flag that indicates whether to close the menu when a value is selected
   */
  @Input() closeOnSelect = true;
  /**
   * Flag that indicates whether to allow to search for value. Default from ng-select false.
   */
  @Input() searchable = false;
  /**
   * Flag that indicates whether to allow to clear selected value.
   */
  @Input() clearable = false;
  /**
   * Placeholder text
   */
  @Input() placeholder = '';
  /**
   * Flag that indicates loading state from the outside (async items loading)
   */
  @Input() loading = false;
  /**
   * Flag that indicates opening direction of dropdown (bottom | top | auto)
   */
  @Input() dropdownPosition = 'auto';
  /**
   * Flag that indicates whether `scrollEnd` event should be emitted
   */
  @Input() disableScrollEnd = false;
  /**
   * Flag that indicates whether filtering of items is allowed.
   */
  @Input() filterItems = false;
  /**
   * Flag that indicates whether to mark first item as focused when opening/filtering.
   */
  @Input() markFirst = false;
  /**
   * Allow to group items by key or function expression
   */
  @Input() groupByFn: (item: any) => any = () => undefined;
  /**
   * Function expression to provide group value
   */
  @Input() groupValueFn = (name: string, children: any[]) => ({
    name,
    total: children.length,
  });

  onSearch(event: string) {
    this.search.emit(event);
  }

  onClear() {
    this.clearSearch.emit();
  }

  onScrollToEnd() {
    if (!this.disableScrollEnd) {
      this.scrollToEnd.emit();
    }
  }

  onRemove(event: any) {
    this.remove.emit(event);
  }

  onChange(event: any) {
    this.change.emit(event);
  }

  selectAll() {
    this.selectedItems = this.items;
    this.onChange(this.selectedItems);
  }

  unselectAll() {
    this.selectedItems = [];
    this.onChange(this.selectedItems);
  }
}
