import { Component, Input, TemplateRef, ViewChild } from '@angular/core';
import { ResponsiveView } from '@backbase/product-summary-common-ang';

@Component({
  selector: 'bb-credit-limit-attribute',
  templateUrl: './credit-limit-attribute.component.html',
})
export class CreditLimitAttributeComponent {
  /**
   * Custom value template reference
   */
  @ViewChild('contentRef') contentRef: TemplateRef<any> | string = '';
  /**
   * Attribute title
   */
  @Input() title = '';
  /**
   * Attribute color.
   * One of the: "primary", "secondary", "accent", "warning", "danger"
   */
  @Input() color: string | undefined;
  /**
   * Currency of the amount
   */
  @Input() currency: string | undefined;
  /**
   * Attribute amount
   */
  @Input() amount: number | undefined;
  /**
   * Defines whether to color the amount field
   *
   * @default false
   */
  @Input() isAmountColored = false;
  /**
   * Whether currency should be displayed as a sign.
   *
   * @default true.
   */
  @Input() useShortCurrency = true;
  /**
   * Specifies whether to use small screen layout
   *
   * @default false.
   */
  @Input() isSmallScreen = false;
  /**
   * Specifies whether to use extra small screen layout
   *
   * @default false.
   */
  @Input() isExtraSmallScreen = false;

  /**
   * Specifies whether container size is small
   */
  @Input() responsiveView: ResponsiveView = 'screen';
}
