export enum CustomColorName {
  POSITIVE = 'positive',
  NEGATIVE = 'negative',
}

export interface CustomColors {
  name: CustomColorName;
  value: string;
}

export interface BalanceHistoryChartCustomizableDirectiveContext {
  autoScale: boolean;
  dataSeries: any[];
  gradient: boolean;
  customColors: CustomColors[] | undefined;
  xAxis: boolean;
  yAxis: boolean;
  legend: boolean;
  showXAxisLabel: boolean;
  showYAxisLabel: boolean;
  xAxisLabel: boolean;
  yAxisLabel: boolean;
  animations: boolean;
}
