import { Component, Input, Output, EventEmitter } from '@angular/core';
import { PaginationType } from '@backbase/product-summary-common-ang';

@Component({
  selector: 'bb-product-summary-footer-ui',
  templateUrl: './product-summary-footer.component.html',
})
export class ProductSummaryFooterComponent {
  /**
   * Current page items number
   */
  @Input() page: number | undefined;
  /**
   * Total number of items
   */
  @Input() count: number | undefined;
  /**
   * Number of items per page
   */
  @Input() itemsPerPage: number | undefined;
  /**
   * Type of pagination (load-more or regular pagination)
   */
  @Input() paginationType: PaginationType | string | undefined;
  /**
   * The flag to indicate if there are more items
   */
  @Input() hasMore: boolean | undefined;
  /**
   * The flag to indicate loading state
   */
  @Input() loading: boolean | undefined;
  /**
   * If `true`, the "First" and "Last" page links are shown.
   */
  @Input() boundaryLinks = true;
  /**
   * If `true`, the "Next" and "Previous" page links are shown.
   */
  @Input() directionLinks = false;
  /**
   * The maximum number of pages to display.
   */
  @Input() maxNavPages: number | undefined;
  /**
   * Event (Output) that emits when page prop is changed
   */
  @Output() pageChange: EventEmitter<number> = new EventEmitter<number>();
  /**
   * Event (Output) that emits when load-more
   */
  @Output() loadMore: EventEmitter<any> = new EventEmitter<any>();
  PaginationType = PaginationType;

  onPageChange(page: number): void {
    this.pageChange.emit(page);
  }

  onLoadMore(): void {
    if (!this.loading) {
      this.loadMore.emit();
    }
  }
}
