import {
  Component,
  ChangeDetectionStrategy,
  Directive,
  Input,
  Output,
  EventEmitter,
  ElementRef,
  ChangeDetectorRef,
} from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Product, ProductKindUri, ResponsiveContainerComponent } from '@backbase/product-summary-common-ang';
import { BbDatePipe } from '@backbase/ui-ang/date-pipe';

@Component({
  selector: 'bb-product-needs-attention-item-ui',
  templateUrl: './product-needs-attention-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ProductNeedsAttentionItemComponent extends ResponsiveContainerComponent {
  readonly hostRef = this;
  readonly productKindUri = ProductKindUri;
  /**
   * Product summary Item  data that this component displays.
   */
  @Input() item: Product | undefined;

  /**
   * A flag specifies whether to display logo
   */
  @Input() showLogo: boolean | undefined;

  /**
   * Emits ProductKind when selected
   */
  @Output() selectProductKind: EventEmitter<any> = new EventEmitter();

  /**
   * Emits arrangement id when pay now is clicked
   */
  @Output() payNowClick: EventEmitter<string> = new EventEmitter();
  /**
   * Localisation for statusBadge
   */
  get statusBadges() {
    return {
      overdueWithDate: $localize`:Status badge for overdue accounts with date|status badge for overdue accounts with date@@product-summary-ui.product-needs-attention-item.badge.overdueWithDate:${
        this.item?.paymentsPastDue ?? ''
      } Payments Missed Since ${this.datePipe.transform(this.item?.overdueSince, 'MMM d, yyyy')}`,
      overdueWithoutDate: $localize`:Status badge for overdue accounts without date|status badge for overdue accounts without date@@product-summary-ui.product-needs-attention-item.badge.overdueWithoutDate:${
        this.item?.paymentsPastDue ?? ''
      } Payments Missed`,
      overdrawnWithDate: $localize`:Status badge for overdrawn accounts with date|status badge for overdrawn accounts with date@@product-summary-ui.product-needs-attention-item.badge.overdrawnWithDate:Overdrawn Since ${this.datePipe.transform(
        this.item?.overdueSince,
        'MMM d, yyyy',
      )}`,
      overdrawnWithoutDate: $localize`:Status badge for overdrawn accounts without date|status badge for overdrawn accounts without date@@product-summary-ui.product-needs-attention-item.badge.overdrawnWithoutDate:Overdrawn`,
    };
  }

  constructor(private datePipe: BbDatePipe, element: ElementRef<HTMLElement>, changeDetector: ChangeDetectorRef) {
    super(element, changeDetector);
  }

  /**
   * Emits `selectProductKind` event
   */
  onSelectProductKind() {
    this.selectProductKind.emit(this.item);
  }
}

@Directive({
  selector: '[bbProductNeedsAttentionItemCustomizable]',
})
export class ProductNeedsAttentionItemCustomizableDirective extends BbTemplate<
  ProductNeedsAttentionItemComponent,
  undefined
> {}
